/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  height: 100%;
  margin: 0;
  line-height: 1.5;
  color: #CE94D8;
}

#error-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

#error-page > h1 {
  font-weight: 800;
  font-size: 40px;
}

.headerContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 50px 100px;

}

.container {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

#headerLinks {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 45%;
  height: 100%;
  list-style: none;
}

li {
  height: 100%;
  display: flex;
  align-items: center;
}

.headerLink:link, .headerLink:visited {
  color: #CE94D8;
}

a:hover, .headerLink:hover {
  cursor: pointer;
  color: #fff1fd;
}

a, .headerLink {
  text-decoration: none;
}

.headerLink {
  height: 70%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}


h1 {
  font-size: 40px;
  font-weight: 700;
}

.btn {
  background-color: #CE94D8;
  width: 165px;
  height: 60px;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn:hover {
  background-color: #fff1fd;
}

i {
  font-style: italic;
  color: gray;
  font-size: 16px;
}

.btn {
  height: 60px;
  width: 165px;
  color: white;
}

.btn:hover {
  color: #CE94D8;
  cursor: pointer;
}

.divider {
  width: 100%;
  height: 300px;
}

h2 {
  font-size: 40px;
  font-weight: 800;
  line-height: normal;
  margin-top: 75px;
}

.pageContainer {
  margin: 0 100px;
  align-items: start;
}

#homeText {
  width: 50%;
  display: flex;
  flex-direction: column;
  text-align: start;
  justify-content: flex-start;
  height: 100%;
}

p {
  color: black;
  font-size: 25px;
  margin: 15px 0;
}

span {
  font-weight: 600;
}

.btn {
  border: none;
}

#waitlist {
  font-weight: 600;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.2);
}

#demoBox, #waitlistBox {
  font-size: 16px;
}

.verticalContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

#iconsContainer {
    width: 36%;
    height: 500px;
}

.icon {
    max-width: 20%;
    height: auto;
}

.iconText {
    width: 75%;
    margin-left: 18px;
}

.iconText h3 {
    font-size: 35px;
    font-weight:800;
    line-height: 45px;
}

.iconText p {
    font-size: 16px;
}

#businessPage {
    margin-top: 60px;
}

.individualIconContainer {
    align-items: start;
    justify-content: space-evenly;
}

strong {
  font-weight: 600;
}

.pilotPage {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 0 100px;
}

.pageText {
  width: 100%;
  text-align: center;
}

p.pageText {
  font-size: 40px;
}

.dropdown, .dropdown:focus {
  width: 30%;
  height: 50px;
  text-align: center;
  border: lightgray 1px solid;
  border-radius: 15px;
  font-size: 25px;
  font-weight: 700;
  color: black;
}

.buttonContainer {
  width: 100%;
  display: flex;
  justify-content: center;
}

.noSelect {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.noSelect:focus {
  outline: none !important;
}

.whitespace {
  margin-bottom: 100px;
  width: 100%;
}

.smallWhitespace {
  margin-bottom: 50px;
  width: 100%;
}

#searchSubmit {
  width: 60px;
  background-color: transparent;
  color: black;
}

.searchIcon {
  width: 60px;
  height: auto;
}

.dropdownSearchContainer {
  width: 100%;
  display:flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

div.dropdowns {
  width: 75%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}

div.serviceCardContainer {
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
}

.serviceCard {
  border: solid black 1px;
  padding: 10px 0;
  border-radius: 10px;
  align-items: center;
  width: 300px;
  height: 320px;
}

.serviceCard:hover {
  box-shadow: 0 15px 15px 0 rgba(0,0,0,0.2);
}

.serviceCardPrice, .serviceCardText {
  color: black;
  margin-top: 5px;
  padding: 0 10px;
  text-wrap: pretty;
}

.serviceCardPrice {
  font-size: 30px;
  font-weight: 700;
  margin: 5px;
  text-align:center;
}

.serviceCardText {
  font-size: 15px;
  font-weight: 600;
  text-align:center; 
}

h3.resultsText {
  font-size:25px;
  color: black;
  width: 100%;
  text-align: center;
}

p.resultsText {
  text-align: center;
  font-size: 16px;
}

span.results {
  color: black;
  font-size: 20px;
  line-height: 60px;
}

h3.resultsText {
  margin-top: 20px;
}

.service-rating {
  color: black;
}

div.rating {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: flex-start;
}

.serviceCardLink {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.serviceCardLink > * {
  width: 100%;
}

.tagContainer {
  display: flex;
  flex-wrap: wrap;
  row-gap: 3px;
  justify-content: center;
  margin-top: 10px;
}

.tagText {
  color: white;
  background: #CE94D8;
  width: 80%;
  text-align: center;
  border-radius: 15px;
}

.waitlistForm {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  row-gap: 10px;
  width: 40%;
}

.waitlistInput {
  width: 50%;
  border-radius: 10px;
}

.waitlistLabel {
  width: 28%;
  font-size: 18px;
  font-weight: 500;
}

.formContainer {
  display: flex;
  width: 100%;
  margin-top: 20px;
  justify-content: flex-end;
}

.submit {
  height: 40px;
  font-size: 14px;
}

#root {
  padding-bottom: 50px;
}

.dropdown{
  width: 100%;
  border-radius: 15px;
}


@media only screen and (max-width: 700px)
{
  .headerContainer {
    justify-content: center;
  }
  #pageTitle  {
    width: 100%;
  }

  #waitlistBox {
    width: 150px;
  }

  .waitlistForm {
    width: 100%;
    justify-content: center;
  }

  .waitlistLabel {
    width: 100%;
    text-align: center;
  }

  .waitlistInput {
    height: 30px;
    width: 100%;
  }

  .dropdown, .dropdown:focus {
    width: 100%;
  }

  .pilotPage {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 0 25px;
  }

  #searchSubmit {
    width: 60px;
    background-color: transparent;
    color: black;
  }
  
  .searchIcon {
    width: 60px;
    height: auto;
  }
}